.particles-container {
  position: relative;
  width: 100%;
  height: 100%;
  pointer-events: auto; /* allow hover tracking */
}

.particles-container canvas {
  position: absolute;
  inset: 0;
  width: 100% !important;
  height: 100% !important;
  display: block;
  pointer-events: none; /* let clicks pass through */
}
